import {Vector2Like} from './../../../../../../../@polygonjs/polygonjs/src/types/GlobalTypes';
import {CoreType} from './../../../../../../../@polygonjs/polygonjs/src/core/Type';
import {Ref, watch} from 'vue';
interface LocalStorageOptions {
	modalStartPosition: Ref<Vector2Like>;
	modalSize: Ref<Vector2Like>;
	nodeId: Ref<number | null>;
}
interface LocalStorageData {
	modalStartPosition: Vector2Like;
	modalSize: Vector2Like;
}

const KEY = 'polyNodeUpdateForm-modal-info';

export function SetupLocalStorage(options: LocalStorageOptions) {
	function saveToLocalStorage() {
		const data: LocalStorageData = {
			modalStartPosition: options.modalStartPosition.value,
			modalSize: options.modalSize.value,
		};
		localStorage.setItem(KEY, JSON.stringify(data));
	}
	function clearLocalStorage() {
		localStorage.removeItem(KEY);
	}

	watch(options.modalStartPosition, saveToLocalStorage, {deep: true});
	watch(options.modalSize, saveToLocalStorage, {deep: true});
	watch(options.nodeId, loadFromLocalStorage);

	function loadFromLocalStorage() {
		const current = localStorage.getItem(KEY);
		if (current) {
			try {
				const currentData = JSON.parse(current) as Partial<LocalStorageData> | undefined;
				if (!currentData) {
					return;
				}
				const modalStartPosition = currentData.modalStartPosition;
				const modalSize = currentData.modalStartPosition;
				if (!(modalStartPosition && modalSize)) {
					return;
				}
				if (CoreType.isNumber(modalStartPosition.x) && !CoreType.isNaN(modalStartPosition.x)) {
					options.modalStartPosition.value.x = modalStartPosition.x;
				}
				if (CoreType.isNumber(modalStartPosition.y) && !CoreType.isNaN(modalStartPosition.y)) {
					options.modalStartPosition.value.y = modalStartPosition.y;
				}
				if (CoreType.isNumber(modalSize.x) && !CoreType.isNaN(modalSize.x)) {
					options.modalSize.value.x = modalSize.x;
				}
				if (CoreType.isNumber(modalSize.y) && !CoreType.isNaN(modalSize.y)) {
					options.modalSize.value.y = modalSize.y;
				}
			} catch (err) {}
		}
	}

	return {
		saveToLocalStorage,
		clearLocalStorage,
	};
}
